import React from 'react';
import styles from './Tooltip.module.css';

const Tooltip = ({ content, display, element }) => {
  const x = element ? element.x - 110/2 + 16 : 0;
  const y = element ? element.y + 16 : 0;
  const tooltipClass = y < 67 ? styles.tooltipBottom : styles.tooltip;

  if(y < 67) {

  }
  if (display) {
    return (
      <div className={styles.container}>
        <div className={tooltipClass} style={{left: x, top: y}}>
          <span className={styles.text}>{content}</span>
        </div>
        <div className={styles.arrow} />
      </div>
    );
  }
  return null;
};

export default Tooltip;
